.login {
    background-color: white;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    min-height: 100vh; 
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    .login-panel {
        flex: 5;
        align-self: center;
        > div {
            margin-top: 3rem;
            margin-left: auto;
            margin-right: auto;
            width: 80%;
            max-width: 700px;

            .copyright {
                color: #777;
                @media (max-width: 960px) {
                    text-align: center;
                }
            }
        }
    }

    .back-panel {
        color: rgba(4, 133, 133, 0.39);
        flex: 7;
        background-image: url(../../img/login_back.webp);
        background-size: cover;
        overflow: hidden;
        background-repeat: no-repeat;
        position: relative;
        @media (max-width: 960px) {
            display: none;
        }
    }

    @keyframes ellipse-animation {
        from {
            transform: scale(0);
        }
        to {
            transform: scale(1.2);
        }
    }
    .ellipse1 {
        position: absolute;
        top: -100px;
        left: -200px;
        animation-name: ellipse-animation;
        animation-duration: 3s;
        animation-iteration-count: infinite;
    }
    .ellipse3 {
        position: absolute;
        top: -40px;
        right: -200px;
        animation-name: ellipse-animation;
        animation-duration: 7s;
        animation-iteration-count: infinite;
    }
    .ellipse2 {
        position: absolute;
        bottom: 250px;
        left: 200px;
        animation-name: ellipse-animation;
        animation-duration: 5s;
        animation-iteration-count: infinite;
    }
}
