.solutions {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    min-height: 100vh;
    .header-panel {
        width: 100%;
        background: url(../img/header_back.webp), rgba(0, 0, 0, 0.5);
        background-blend-mode: darken;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        position: relative !important;
        z-index: 100;
        width: 100%;
    }
    .text-stroke {
        z-index: 1;
        text-align: center;
        color: transparent;
        transform: scale(1.5);
        top: -15%;
        position: absolute;
        -webkit-text-stroke: 1px rgba(255, 255, 255, 0.1);
    }
    .section-1 {
        background-color: #0e0e0e;
        padding: 5rem;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
        .soft-card {
            min-height: 10px;
            cursor: pointer;
            // min-width: 350px;
            padding: 5em 3em 3em;
            margin: 1em 0.5em;
            background-color: #2f2f2f;
            border-radius: 10px;
            display: block;
            word-wrap: break-word;
            z-index: 100;
            height: 90%;
            position: relative;
            transition: 0.2s;
            color: var(--font-white-color);
            a {
                color: var(--font-white-color);
            }
            &:hover,
            &.active {
                background-color: var(--default-color);
                color: var(--font-black-color);
                a {
                    color: var(--font-black-color);
                }
                &::after {
                    position: absolute;
                    bottom: 10px;
                    transition: 0.2s;
                    right: 10px;
                    background-color: var(--font-white-color);
                    content: "";
                    width: 100%;
                    height: 100%;
                    z-index: -1;
                    border-radius: 10px;
                }
            }

            &.row2 {
                transform: translateX(5em);
                @media (max-width: 1200px) {
                    transform: translateX(0);
                }
            }
        }
    }
    .section-2 {
        background-color: #272727;
        padding: 5rem;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
    }
    @keyframes ellipse-animation {
        0% {
            transform: scale(0);
        }
        50% {
            transform: scale(1);
        }
        70% {
            transform: scale(1);
        }
        100% {
            transform: scale(0);
        }
    }
    .ellipse1 {
        position: fixed;
        left: -300px;
        top: 30%;
        z-index: 1;
        transform: scale(0);
        animation-name: ellipse-animation;
        animation-duration: 5s;
        animation-delay: 7s;
        animation-iteration-count: infinite;
    }
    .ellipse2 {
        position: fixed;
        right: -10%;
        top: 50%;
        transform: scale(0);
        z-index: 1;
        animation-name: ellipse-animation;
        animation-delay: 2s;
        animation-duration: 10s;
        animation-iteration-count: infinite;
    }
    .ellipse3 {
        position: fixed;
        left: -200px;
        transform: scale(0);
        top: -30%;
        z-index: 1;
        animation-name: ellipse-animation;
        animation-duration: 15s;
        animation-delay: 5s;
        animation-iteration-count: infinite;
    }
}
