body {
    background-color: black !important;
    width: 100%;
}
.landing {
    overflow: hidden;
    width: 100%;
    .header-panel {
        background-color: transparent;
        background-blend-mode: darken;
        background-size: inherit;
        background-position: center center;
        background-repeat: no-repeat;
        z-index: 100;
        width: 100%;
        position: fixed;
    }
    .section-1 {
        min-height: 100vh;
        padding: 1em;
        width: 100%;
        background: url(../img/landing-back.webp) no-repeat, rgba(0, 0, 0, 0.6);
        background-blend-mode: darken;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        margin: 0;
        position: relative;
        padding: 0;
        overflow: hidden;
        .img-yo {
            width: 60%;
            position: absolute;
            right: -200px;
            bottom: -50px;
            user-select: none;
            z-index: 0;
            animation-name: yo-animation;
            animation-duration: 6s;
            animation-iteration-count: infinite;
            animation-fill-mode: both;
            animation-delay: 3s;
            @keyframes yo-animation {
                0% {
                    transform: scale(0.7);
                }
                50% {
                    transform: scale(1.2);
                }
                80% {
                    opacity: 0.5;
                }
                100% {
                    transform: scale(0.7);
                }
            }
        }
        .title-panel {
            justify-content: center;
        }
        .ask-panel {
            z-index: 10;
            background-color: white;
            border-radius: 10px;
            padding: 1em 2em 1em 2em;
            max-width: 500px;
            min-width: 300px;
            width: 100%;
            margin-bottom: 1rem;
            margin-right: 30%;
            color: var(--font-black-color);
            box-shadow: 7px 5px 7px var(--default-light-color);

            @media (max-width: 1024px) {
                margin-bottom: 1rem;
                margin-right: auto;
                margin-left: auto;
            }
            div.radio-label {
                position: relative;
                cursor: pointer;
                font-size: 1.1rem;
                padding-left: 25px;
                user-select: none;
                &:hover .radio {
                    // background-color: var(--default-color);
                    outline: 2px solid var(--default-color);
                }
            }
            .radio {
                position: absolute;
                left: 0px;
                top: 7px;
                border: 1px solid var(--default-color);
                border-radius: 10%;
                width: 12px;
                height: 12px;
                content: "";
                &.checked {
                    background-color: var(--default-color);
                }
            }
        }
    }
    .section-2 {
        background-color: #101010;
        min-height: 400px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        position: relative;
        background-image: url(../img/section_2.webp);
        background-position: left;
        background-size: contain;
        background-repeat: no-repeat;
        height: 100%;
        @media (max-width: 1024px) {
            background-image: none;
        }
        .des-panel {
            flex: 9;
            align-self: center;
            padding: 1rem 3rem;
            align-items: center;
            margin-left: auto;
            margin-right: auto;
            .des-item {
                padding: 1em;
                flex: 3;
                display: flex;
                justify-items: flex-start;
                justify-content: flex-start;
                flex-direction: row;
                img {
                    margin: 3rem 1em;
                    border: 2px solid var(--font-white-color);
                    padding: 1em;
                    border-radius: 50%;
                    width: 70px;
                    min-width: 70px;
                    min-height: 70px;
                    height: 70px;
                }
                @media (max-width: 768px) {
                    text-align: center;
                    flex-direction: column;
                    img {
                        margin: 0 auto;
                    }
                }
            }
        }
    }
    .section-3 {
        background-color: #202020;
        padding: 3em;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
        .work-card {
            background-color: var(--default-light-color);
            border-radius: 10px;
            color: var(--font-black-color);
            padding: 1.5em 1.4em;
            width: 100%;
            height: 100%;
            position: relative;
            cursor: pointer;
            text-align: center;
            margin: 1em 0;
            transition: 0.3s;
            &:hover {
                transform: translateY(-5px);
                background-color: #eee8cbd5;
            }
        }
    }
    .section-4 {
        background-color: #101010;
        padding: 3em;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
    }
    .section-5 {
        background-color: #202020;
        padding: 5em 8em;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
        .client-link {
            cursor: pointer;
            transition: 0.3s;
            text-align: center;
            width: 100px;
            height: 60px;
            margin: 1em;
            &:hover {
                transform: translateY(-10px);
            }
        }
    }

    .text-stroke {
        z-index: 0;
        text-align: center;
        color: transparent;
        transform: scale(1.5);
        left: calc(50% - 3em);
        top: -15%;
        position: absolute;
        -webkit-text-stroke: 1px rgba(255, 255, 255, 0.1);
    }

    @keyframes ellipse-animation {
        0% {
            transform: scale(0);
        }
        50% {
            transform: scale(1);
        }
        70% {
            transform: scale(1);
        }
        100% {
            transform: scale(0);
        }
    }

    .ellipse1 {
        position: fixed;
        left: -400px;
        top: 30%;
        transform: scale(0);
        animation-name: ellipse-animation;
        animation-duration: 5s;
        animation-delay: 7s;
        animation-iteration-count: infinite;
        z-index: 0;
    }
    .ellipse2 {
        position: fixed;
        left: 3%;
        top: 10%;
        transform: scale(0);
        animation-name: ellipse-animation;
        animation-delay: 10s;
        animation-duration: 16s;
        animation-iteration-count: infinite;
        z-index: 0;
    }
    .ellipse3 {
        position: fixed;
        left: -200px; 
        transform: scale(0);
        top: -50%;
        animation-name: ellipse-animation;
        animation-duration: 15s;
        animation-delay: 5s;
        animation-iteration-count: infinite;
        z-index: 0;
    }
    .ellipse4 {
        position: fixed;
        right: -120px;
        bottom: -20%;
        transform: scale(0);
        animation-name: ellipse-animation;
        animation-delay: 1s;
        animation-duration: 10s;
        animation-iteration-count: infinite;
        z-index: 0;
    }

    @media (max-width: 1200px) {
        .ellipse1,
        .ellipse2,
        .ellipse3, 
        .ellipse4 {
            display: none;
        }
    }
    .stroke-number {
        font-size: 14em;
        line-height: 0;
        color: transparent;
        -webkit-text-stroke: 3px var(--default-color);
        cursor: pointer;
        animation-name: stroke-animation;
        animation-duration: 3s;
        animation-iteration-count: infinite;
        @keyframes stroke-animation {
            from {
                color: transparent;
            }
            to {
                color: var(--default-color);
            }
        }
    }
    .landing-choose-image {
        position: absolute;
        height: 95%;
        z-index: 3;
        width: auto;
        border-radius: 50%;
        top: 3%;
        left: 2%;
        transition: 2s;
        cursor: pointer;
    }
    .round-desc-1 {
        @media (max-width: 1024px) {
            transform: none !important;
        }
    }
    .choose-image-pan {
        overflow: hidden;
        border-radius: 50%;
        position: relative;
        min-width: 300px;
        min-height: 400px;
        max-width: 400px;
        width: 100%;
        height: auto;
        max-height: 500px;
        background-size: 100% 100%;
        background-repeat: no-repeat;
        transition: 1s;
        background-image: url(../img/round.webp);
        @media (max-width: 1200px) {
            margin-left: auto;
            margin-right: auto;
            transform: none !important;
            width: 250px;
            min-width: 250px;
            min-height: 250px;
            height: 250px;
        }
        &:hover img {
            transform: scale(1.3) rotate(10deg);
        }
    }
}
