.hosting {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    min-height: 100vh;
    .header-panel {
        width: 100%;
        background: url(../img/header_back.webp), rgba(0, 0, 0, 0.5);
        background-blend-mode: darken;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        position: relative !important;
    }
    .text-stroke {
        z-index: 1;
        text-align: center;
        color: transparent;
        transform: scale(1.5);
        word-break: keep-all;
        top: -15%;
        position: absolute;
        -webkit-text-stroke: 1px rgba(255, 255, 255, 0.1);
    }

    .section-1 {
        background-color: #1a1a1a;
        padding: 8rem;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
        .price-card {
            border-radius: 5px;
            min-width: 500px;
            user-select: none;
            border: 1px solid var(--default-color);
            color: var(--font-white-color);
            cursor: pointer;
            box-shadow: 1px 4px 9px var(--default-color);
            transition: 0.3s;
            margin: 1em;
            &:hover {
                border-left: 14px solid var(--default-color);
                transform: translateX(20px);
            }
            .header {
                padding: 2em 3em;
                background-color: #131313;
            }
            h3, h4{
                padding: 0;
                margin: 0;
            }
            .body {
                padding: 2em 3em;
                background-color: #272727;
                img {
                    width: 70px;
                }
                @media (max-width: 1024px) {
                    img {
                        width: 50px;
                    }
                }
            }
        }
    }
    .desc-item {
        position: relative;
        padding: 0 1.5rem;
        cursor: pointer;
        margin: 1em;
        &:hover {
            span {
                transform: scale(1.3);
                border: 3px solid transparent;
            }
            color: var(--font-light-color);
        }
        label,
        h1,
        h2,
        h3,
        h4 {
            cursor: pointer;
        }
        span {
            transition: 0.3s;
            position: absolute;
            width: 20px;
            height: 20px;
            z-index: 1;
            border-radius: 50%;
            top: 3px;
            left: 5px;
            background-color: var(--default-color);
        }
    }

    .section-2 {
        padding: 8rem;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
        background-color: #272727;
        .future-card {
            background-color: var(--font-white-color);
            padding: 2em;
            display: flex;
            cursor: pointer;
            user-select: none;
            margin: 1em auto;
            transition: 0.2s;
            height: 100%;
            border-radius: 10px;
            box-shadow: 1px 3px 15px var(--default-color);
            width: 100%;
            @media (max-width: 1360px) {
                padding: 1em !important;
                margin: 0.5em;
            }
            img {
                flex: 4;
            }
            div {
                flex: 8;
            }
            a {
                transition: 0.2s;
            }
            &:hover,
            &.active {
                background-color: var(--default-light-color);
                a {
                    transform: translateX(20px);
                }
            }
        }
    }
    .section-3 {
        padding: 8rem;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
        background-color: #1a1a1a;
        .future-card {
            background-color: var(--font-white-color);
            padding: 2em;
            text-align: center;
            width: 100%;
            height: 100%;
            margin: 1em auto;
            cursor: pointer;
            transition: 0.2s;
            border-radius: 10px;
            box-shadow: 1px 3px 15px var(--default-color);
            img {
                flex: 4;
                padding: 1em;
            }
            div {
                flex: 8;
            }
            a {
                transition: 0.2s;
            }
            &:hover,
            &.active {
                background-color: var(--default-light-color);
                transform: translateX(10px);
                a {
                    transform: translateX(20px);
                }
            }
        }
    }
    .section-4 {
        background-color: #272727;
        padding: 8rem;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
    }

    .section-5 {
        background-color: #1a1a1a;
        padding: 5rem;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
        .faq-panel {
            border-radius: 10px;
            overflow: hidden;
            @media (max-width: 1024px) {
                padding: 0 !important;
                margin: 0;
            }
            .title-bar {
                background-color: var(--font-white-color);
                color: var(--font-black-color);
                padding: 1em 2em;
                font-size: 1.3em;
                position: relative;
                border-bottom: 1px solid #272727;
                cursor: pointer;
                z-index: 100;
                &:hover {
                    box-shadow: 1px 2px 4px #272727;
                    background-color: var(--default-color);
                    color: var(--font-white-color);
                }
                &::after {
                    content: "+";
                    font-size: 2em;
                    position: absolute;
                    right: 0.5em;
                    top: 0px;
                }
                &.active {
                    background-color: var(--default-color);
                    &::after {
                        content: "-";
                        font-size: 2em;
                        position: absolute;
                        right: 0.7em;
                        top: 0px;
                    }
                }
            }
            .faq-desc {
                z-index: 50;
                background-color: #272727;
                display: none;
                &.active {
                    display: block;
                }
            }
        }
    }

    .section-6 {
        background-color: #272727;
        padding: 5rem;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
    }
    @keyframes ellipse-animation {
        0% {
            transform: scale(0);
        }
        50% {
            transform: scale(1);
        }
        70% {
            transform: scale(1);
        }
        100% {
            transform: scale(0);
        }
    }
    .ellipse1 {
        position: fixed;
        left: -100px;
        top: 30%;
        z-index: 1;
        transform: scale(0);
        animation-name: ellipse-animation;
        animation-duration: 5s;
        animation-delay: 7s;
        animation-iteration-count: infinite;
    }
    .ellipse2 {
        position: fixed;
        right: 10%;
        top: 50%;
        transform: scale(0);
        z-index: 1;
        animation-name: ellipse-animation;
        animation-delay: 2s;
        animation-duration: 10s;
        animation-iteration-count: infinite;
    }
    .ellipse3 {
        position: fixed;
        left: 200px;
        transform: scale(0);
        top: 30%;
        z-index: 1;
        animation-name: ellipse-animation;
        animation-duration: 15s;
        animation-delay: 5s;
        animation-iteration-count: infinite;
    }
}
