.contactus {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    overflow: hidden;
    .header-panel {
        width: 100%;
        background: url(../img/header_back.webp), rgba(0, 0, 0, 0.5);
        background-blend-mode: darken;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        position: relative !important;
    }
    .text-stroke {
        @media (max-width: 1200px) {
            display: none;
        }
        z-index: 1;
        text-align: center;
        color: transparent;
        transform: scale(1.5);
        top: -20%;
        position: absolute;
        -webkit-text-stroke: 1px rgba(146, 146, 146, 0.5);
    }

    div.radio-label {
        position: relative;
        cursor: pointer;
        font-size: 1.1rem;
        padding-left: 25px;
        user-select: none;
        color: var(--font-black-color);
        &:hover .radio {
            // background-color: var(--default-color);
            outline: 2px solid var(--default-color);
        }
    }
    .radio {
        position: absolute;
        left: 0px;
        top: 7px;
        border: 1px solid var(--default-color);
        border-radius: 10%;
        width: 15px;
        height: 15px;
        color: var(--font-black-color);
        content: "";
        &.checked {
            background-color: var(--default-color);
        }
    }

    .section-1 {
        background-color: #fbfbfb;
        padding: 8rem;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
        .contact-card {
            border-radius: 10px;
            text-align: center;
            padding: 2em 0 1em 0; 
            margin: 2em 1em;
            cursor: pointer;
            color: var(--font-black-color);
            border: 1px solid rgb(187, 187, 187);
            box-shadow: 1px 3px 5px var(--default-color);
            border: 1px solid var(--default-color);
            transition: 0.3s;
            position: relative;
            .contact-type {
                position: absolute;
                width: 70px;
                height: 70px;
                padding: 1em;
                background-color: white;
                border-radius: 50%;
                border: 1px solid var(--default-color);
                top: -35px;
                left: calc(50% - 35px);
            }
            &:hover {
                background-color: var(--default-light-color);
            }
        }
    }
    .section-2 {
        background-color: #e9c8df;
        background-image: url(../img/portrait-female.webp);
        background-size: cover;
        padding: 8rem;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
    }
    .section-3 {
        background-color: #e0e0e0;
        padding: 3em;
    }
    .section-4 {
        background-color: white;
        padding: 3em;
        @media (max-width: 1200px) {
            padding: 3em !important;
        }
        @media (max-width: 1024px) {
            padding: 1em !important;
        }
        .connect-card {
            padding: 2em;
            margin: 1em;
            background-color: #c5c5c5;
            border-radius: 5px;
            cursor: pointer;
            transition: 0.3s;
            &:hover {
                transform: translateY(-10px);
            }
        }
    }
    .section-5 {
        background-color: #272727;
        padding: 3em;
        .soft-card{
            background-color: #f0f0f0;
            border-radius: 5px;
            cursor: pointer;
            margin: 1em;
            padding: 0 2em 1em 2em;
            color: var(--font-black-color);
            direction: ltr;
            &:hover{
                box-shadow: 1px 3px 5px var(--default-color);
            }
            &.active{
                background-color: var(--default-light-color);
            }
        }
    }
    .desc-item {
        position: relative;
        padding: 0 1.5rem;
        cursor: pointer;
        margin: 1em;
        &:hover {
            span {
                transform: scale(1.3);
                border: 3px solid transparent;
            }
            color: var(--font-light-color);
        }
        label,
        h1,
        h2,
        h3,
        h4 {
            cursor: pointer;
        }
        span {
            transition: 0.3s;
            position: absolute;
            width: 20px;
            height: 20px;
            z-index: 1;
            border-radius: 50%;
            top: 3px;
            left: 5px;
            background-color: var(--default-color);
        }
    }

    @keyframes ellipse-animation {
        0% {
            transform: scale(0);
        }
        50% {
            transform: scale(1);
        }
        70% {
            transform: scale(1);
        }
        100% {
            transform: scale(0);
        }
    }
    .ellipse1 {
        position: fixed;
        left: -30px;
        top: 80%;
        z-index: -1;
        transform: scale(0);
        animation-name: ellipse-animation;
        animation-duration: 5s;
        animation-delay: 7s;
        animation-iteration-count: infinite;
    }
    .ellipse2 {
        position: fixed;
        right: 10%;
        top: 90%;
        transform: scale(0);
        z-index: 1;
        animation-name: ellipse-animation;
        animation-delay: 2s;
        animation-duration: 10s;
        animation-iteration-count: infinite;
    }
    .ellipse3 {
        position: fixed;
        left: 100px;
        transform: scale(0);
        top: 60%;
        z-index: 1;
        animation-name: ellipse-animation;
        animation-duration: 15s;
        animation-delay: 5s;
        animation-iteration-count: infinite;
    }
}
