.infrastructure {
    margin: 0;
    padding: 0;
    width: 100%;
    overflow: hidden;
    height: 100%;
    min-height: 100vh;
    .header-panel {
        width: 100%;
        background: url(../img/header_back.webp), rgba(0, 0, 0, 0.5);
        background-blend-mode: darken;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        position: relative !important;
    }
    .text-stroke {
        z-index: 1;
        text-align: center;  
        color: transparent;
        transform: scale(1.5);
        top: -15%;
        position: absolute;
        -webkit-text-stroke: 1px rgba(255, 255, 255, 0.1);
    }

    .section-1 {
        background-color: #272727;
        padding: 5rem;
        @media (max-width: 1200px) {
            padding: 3em!important;
        }
        @media (max-width: 1024px) {
            padding: 1em!important;
        }
        .soft-card {
            color: var(--font-black-color); 
            min-height: 10px;
            cursor: pointer;
            padding: 5em 2em 2em;
            background-color: var(--default-light-color);
            border-radius:   10px;
            word-wrap: break-word;
            text-align: center;
            min-width: 320px;
            margin: 1em;
            word-break: keep-all;
            z-index: 100;
            position: relative; 
            height: 100%;
            width: auto;
            box-shadow: 0 1px  4px var(--default-hover-color); 
            border-bottom: 3px solid var(--default-color);
            transition: 0.2s;
            transition: 0.4s;
            img{
                text-align: center;
                width: 100px;
                height: 100px;
            }
            a{
                img{
                    width: 40px;
                    height: 60px;
                }
            }
            .h4{
                color: var(--font-black-color);
                font-size: 1.3em;
            }
            &:hover{
                background-color: var(--font-white-color);
                transform:translateY(-10px);
                box-shadow: 0  2px 8px var(--default-color);
            }
        }
    }
    
    .section-2 {
        background-color: #0e0e0e;
        padding: 5rem;
        @media (max-width: 1200px) {
            padding: 3em!important;
        }
        @media (max-width: 1024px) {
            padding: 1em!important;
        } 
        .fact-card{
            border-radius: 10px;
            text-align: center;
            cursor: pointer;
            background-color:#252526;
            position: relative;
            padding: 0.1em 0.7em;
            transition: 0.3s;
            overflow: hidden;
            margin: 1em;
            img{
                position: absolute;
                width: 100px;
                height: 100px;
                bottom: 10px;
                right: -10px;
            }
            &:hover{
                background-color:#1a1a1a;
                transform:translateY(-10px) scale(1.1);
                box-shadow: 0  2px 8px var(--default-color);
            }
        }
    }
    .section-3 {
        background-color: #272727;
        padding: 5rem;
        @media (max-width: 1200px) {
            padding: 3em!important;
        }
        @media (max-width: 1024px) {
            padding: 1em!important;
        }
    }
    @keyframes ellipse-animation {
        0% {
            transform: scale(0);
        }
        50% {
            transform: scale(1);
        }
        70% {
            transform: scale(1);
        }
        100% {
            transform: scale(0);
        }
    }
    .ellipse1 {
        position: fixed;
        left: -300px;
        top: 30%;
        z-index: 1;
        transform: scale(0);
        animation-name: ellipse-animation;
        animation-duration: 5s;
        animation-delay: 7s;
        animation-iteration-count: infinite;
    }
    .ellipse2 {
        position: fixed;
        right: -10%;
        top: 50%;
        transform: scale(0);
        z-index: 1;
        animation-name: ellipse-animation;
        animation-delay: 2s;
        animation-duration: 10s;
        animation-iteration-count: infinite;
    }
    .ellipse3 {
        position: fixed;
        left: -200px;
        transform: scale(0);
        top: -30%;
        z-index: 1;
        animation-name: ellipse-animation;
        animation-duration: 15s;
        animation-delay: 5s;
        animation-iteration-count: infinite;
    }
}
