.side-menu-panel {
	position: fixed;
	top: 0;
	height: 100vh;
	overflow-y: auto;
	padding: 2em;
	border-left: 3px solid var(--default-color);
	background-color: #272727;
	width: 500px;
	z-index: 1500;
	transition: 0.3s;
	@media (max-width: 1024px) {
		width: 100%;
	}
	right: -105%;
	&.show {
		right: 0;
	}
	&.hide {
		right: -105%;
	}
	.header-bar {
		// margin-bottom: 3em;
		display: flex;
		justify-content: flex-end;
		flex-direction: row;
		.btn-close {
			font-size: 3em;
			cursor: pointer;
			&:hover {
				color: var(--default-light-color) !important;
			}
		}
	}
	.menu-panel {
		.menu {
			margin: 0.5em 0;
			user-select: none;
			color: var(--default-color);
			font-size: 2em;
			font-weight: bold;
			display: flex;
			position: relative;
			justify-content: space-between;
			.menu-text {
				transition: 0.3s;
				cursor: pointer;
				&:hover {
					color: var(--default-light-color) !important;
				}
			}
			.open-mark {
				transition: 0.3s;
				color: var(--default-light-color) !important;
				cursor: pointer;
				&:hover {
					color: var(--default-light-color) !important;
				}
			}
			&::after {
				content: "+";
				font-size: 1em;
				position: absolute;
				right: 0.5em;
				top: 0px;
				color: white;
			}
			&.active {
				&::after {
					color: white;
					content: "-";
					font-size: 1.2em;
					position: absolute;
					right: 0.6em;
					top: 0px;
				}
			}
		}
		.menu-items {
			height: 0;
			overflow: hidden;
			&.active {
				height: 100%;
			}
			.submenu {
				font-size: 1.2em;
				display: block;
				color: var(--font-white-color);
				transition: 0.3s;
				padding-left: 2em;
				position: relative;
				margin: 10px;
				user-select: none;
				cursor: pointer;
				&:hover {
					color: var(--default-light-color) !important;
				}
				&::before {
					position: absolute;
					width: 0.8em;
					height: 0.8em;
					left: 1.5em;
					z-index: -1;
					top: 0.4em;
					background-color: var(--default-color);
					content: "";
					border-radius: 50%;
					display: block;
				}
			}
		}
	}

	.footer-bar {
		position: absolute;
		bottom: 0;
		overflow: hidden;
		left: 0;
		// height: 50px;
		background-color: #161616;
		padding: 0 1em;
		width: 100%;
		.btn-login {
			padding: 0.7em 1em;
			border-right: 2px solid rgb(102, 102, 102);
			cursor: pointer;
			color: var(--default-color) !important;
			cursor: pointer;
			display: flex;
			font-size: 1.2em;
			&:hover {
				label {
					color: white !important;
				}
			}
			img {
				margin-right: 1em;
			}
